import React, {useState, useEffect} from 'react'
import {compose} from 'redux'
import {firestoreConnect} from 'react-redux-firebase'
import {connect} from 'react-redux'
import axios from 'axios'
import {LinkContainer} from 'react-router-bootstrap'

const Team = props => {
    const [players, setPlayers] = useState([])
    const [orgTeam, setOrgTeam] = useState([])
    
    const {users, team} = props
   
    // const options = {
    //     method: 'GET',
    //     url: 'https://api-football-v1.p.rapidapi.com/v3/players',
    //     params: {team: teamId, season: '2020', page: '2', league: '4'},
    //     headers: {
    //       'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
    //       'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
    //     }
    // }

    const options = {
        method: 'GET',
        url: 'https://api-football-v1.p.rapidapi.com/v3/players',
        params: {team: '768', season: '2020', page: '2'},
        headers: {
          'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
          'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
        }
    };

    let API = "APIkey=4902286b10445514873d2fc58d7cb100ee45cca2ec1926aa8cf3df082a1a7441";
    useEffect(()=>{

        const getData = async () => {
        const pl = await axios.get(`https://apiv2.allsportsapi.com/football/?&met=Teams&leagueId=1&${API}`)
        setOrgTeam(pl.data.result[0].players)
        
        }
        getData()
        
    }, [])

    useEffect(()=>{
        const getData = async () => {
            const pr = await axios.request(options)
            setPlayers(pr.data.response)
            
        }
        getData()
    }, [])
    

    if(team){
        return (
            <div>Your Team</div>
        )
    }else{
        return(
           <div className='d-flex flex-column justify-content-center align-items-center' style={{height:"80vh"}}>
               <h1 className='text-center'>
                   You have not <br /> Fantasy Football Team
               </h1>
               
               <LinkContainer to='newteam'>
               <button className='btn btn-success btn-large'>
                   Create Fantasy Football Team
               </button>
               </LinkContainer>
           </div>
        )
    }
}

export default compose(
    firestoreConnect([{collection : 'users'}]),
    connect((state, props)=>({
        users : state.firestore.ordered.users,
        team : state.firebase.profile.team
    }))
)(Team)
