import React from 'react'
import {Provider} from 'react-redux'
import {ReactReduxFirebaseProvider} from 'react-redux-firebase'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import AppNavbar from './Components/AppNavbar'
import store,{rrfProps} from './Store/configureStore'
import {UserIsAuthenticated, UserIsNotAuthenticated} from './helpers/auth'
import HomepageT from './Components/HomepageT'
import HomepageF from './Components/HomepageF'
import Log from './Components/LogIn/Log'
import Team from './Components/Team'
import Reg from './Components/LogIn/Reg'
import NewTeam from './Components/NewTeam'

const App = () => {
  return (
    <Provider store={store}>
      <ReactReduxFirebaseProvider {...rrfProps}>
        <Router>
          <AppNavbar />

          <Switch>
            <Route path='/' exact component={UserIsAuthenticated(HomepageT)} />
            <Route path='/home' exact component={UserIsNotAuthenticated(HomepageF)} />
            <Route path='/log' exact component={UserIsNotAuthenticated(Log)} />
            <Route path='/team' exact component={UserIsAuthenticated(Team)} />
            <Route path='/reg' exact component={UserIsNotAuthenticated(Reg)} />
            <Route path='/newteam' exact component={UserIsAuthenticated(NewTeam)} />
          </Switch>
        </Router>
      </ReactReduxFirebaseProvider>
    </Provider>
  )
}

export default App
