import React, {useState, useEffect} from 'react'
import { Container } from 'react-bootstrap'
import {Navbar, Nav, Button} from 'react-bootstrap'
import {compose} from 'redux'
import {connect} from 'react-redux'
import {firebaseConnect} from 'react-redux-firebase'
import {LinkContainer} from 'react-router-bootstrap'

const AppNavbar = props => {

    const {firebase, auth} = props
    const [isAuth, setIsAuth] = useState(false)

    useEffect(()=>{
        if(auth.uid){
            setIsAuth(true)
        }else{
            setIsAuth(false)
        }
    }, [auth])

    const LogOut = e =>{
        e.preventDefault()

        firebase.logout()
    }

    if(isAuth){
        return (
            <div>
                <Navbar bg="light" expand="lg" className='d-flex direction-column'>
                <Container>
                <Navbar.Brand href="#home">Fantasy Football Team</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className='justify-content-end'>
                <Nav>
                    <LinkContainer to='legue'>
                    <Nav.Link>Legue</Nav.Link>
                    </LinkContainer>
                    <LinkContainer to='team'>
                    <Nav.Link>My Team</Nav.Link>
                    </LinkContainer>
                    <Nav.Link href="https://t.me/joinchat/UNToK4zdMlfYxJ9z" active><i class="fa fa-telegram" style={{fontSize:"25px", color:"blue"}}></i></Nav.Link>
                    
                    <Button onClick={LogOut} className="btn btn-small btn-danger">Log Out</Button>
                    
                    </Nav>
                </Navbar.Collapse>
                </Container>
                </Navbar>
            </div>
        )
    }else{
        return (
            <div>
                <Navbar bg="light" expand="lg" className='d-flex direction-column'>
                <Container>
                <Navbar.Brand href="#home">Fantasy Football Team</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className='justify-content-end'>
                    <Nav>
                    <LinkContainer to='legue'>
                    <Nav.Link>Legue</Nav.Link>
                    </LinkContainer>
                    <Nav.Link href="https://t.me/joinchat/UNToK4zdMlfYxJ9z" active><i class="fa fa-telegram" style={{fontSize:"25px", color:"blue"}}></i></Nav.Link>
                    <LinkContainer to='log'>
                    <Button className="btn btn-small btn-success mx-1">Log In</Button>
                    </LinkContainer>
                    <LinkContainer to='reg'>
                    <Button className="btn btn-small btn-danger">Reg</Button>
                    </LinkContainer>
                    </Nav>
                </Navbar.Collapse>
                </Container>
                </Navbar>
            </div>
        )
    }
}

export default compose(
    firebaseConnect(),
    connect((state, props) => ({
        auth : state.firebase.auth
    }))
)(AppNavbar)
