import React, {useState, useEffect} from 'react'
import {compose} from 'redux'
import {connect} from 'react-redux'
import {firestoreConnect} from 'react-redux-firebase'
import { Button, Form } from 'react-bootstrap'
import _, { indexOf } from 'lodash'
import axios from 'axios'
import foto1 from './img/field6.jpg'
import foto2 from './img/x1.jpg'
import './style/newTeam.css'

const NewTeam = props => {

    const {team, history} = props

    const [teamName, setTeamName] = useState('FC Barcelona')
    const [tactic, setTactic] = useState('4-3-3')
    const [page, setPage] = useState(true)
    const [fr, setFr] = useState([])
    const [def, setDef] = useState([])
    const [mid, setMid] = useState([])
    const [forv1, setForv1] = useState(undefined)
    const [mid1, setMid1] = useState(undefined)
    const [def1, setDef1] = useState(undefined)
    const [seP, setSeP] = useState(false)
    const [country, setCountry] = useState([])
    const [coundId, setCounId] = useState('2')
    const [plId, setPlId] = useState()
    const [players, setPlayers] = useState([])



    useEffect(()=>{
        let APIKEY = '4902286b10445514873d2fc58d7cb100ee45cca2ec1926aa8cf3df082a1a7441'
        const countriesRapid = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/teams',
            params: {league: '4', season: '2020'},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };
        const selectedTeam = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/teams',
            params: {id: `${coundId}`},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };
       
        const getCountry = async() => {
            const coun = await axios.get(`https://apiv2.allsportsapi.com/football/?&met=Teams&leagueId=1&APIkey=${APIKEY}`)
            const counRapid = await axios.request(countriesRapid)
            const selCoun = await axios.request(selectedTeam)
            
            // console.log(plRapid1.data.response)
            // console.log(selCoun.data.response[0].team.name)
            // console.log(coun.data.result.filter(c=> c.team_name === selCoun.data.response[0].team.name)[0].players.map(a=>a.player_name))

            setCountry(counRapid.data.response.filter(c=> coun.data.result.map(m=> m.team_name).includes(c.team.name)))
            
           
        }
        getCountry()
        
    }, [])

    const onPage = e => {
        e.preventDefault()

        if(teamName.trim().length >= 3){
            setPage(false)
            setFr(_.take([1,2,3,4,5], tactic.slice(4,5)))
            setDef(_.take([1,2,3,4,5],tactic.slice(-5,-4)))
            setMid(_.take([1,2,3,4,5],tactic.slice(2,3)))
            
        }else{
            alert("Enter Team Name")
        }
    }

    const PlayerSelect = player => {
        setSeP(true)
        console.log(player)
    }

    const OnChangeCoun = async counId => {
        console.log('xotyabi ishlayopti')
        setCounId(counId)

        let APIKEY = '4902286b10445514873d2fc58d7cb100ee45cca2ec1926aa8cf3df082a1a7441'
        const countriesRapid = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/teams',
            params: {league: '4', season: '2020'},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };
        const selectedTeam = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/teams',
            params: {id: `${counId}`},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
          };
        
        const options2 = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/players',
            params: {team: `${counId}`, season: '2020', page:"1"},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };
        const options3 = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/players',
            params: {team: `${counId}`, season: '2020', page:"2"},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };
        const options4 = {
            method: 'GET',
            url: 'https://api-football-v1.p.rapidapi.com/v3/players',
            params: {team: `${counId}`, season: '2020', page:"3"},
            headers: {
              'x-rapidapi-key': '23ea03a25cmshc0500faa19e674fp1140c4jsn9a9763290cfd',
              'x-rapidapi-host': 'api-football-v1.p.rapidapi.com'
            }
        };

        const getPlayers = async () =>{
            const coun = await axios.get(`https://apiv2.allsportsapi.com/football/?&met=Teams&leagueId=1&APIkey=${APIKEY}`)
            const selCoun = await axios.request(selectedTeam)
            const plRapid1 = await axios.request(options2)
            const plRapid2 = await axios.request(options3)
            const plRapid3 = await axios.request(options4)
            let pls1 = plRapid1.data.response.filter(mut=> coun.data.result.filter(c=> c.team_name === selCoun.data.response[0].team.name)[0].players.map(c=> c.player_name).includes(mut.player.name))
            let pls2 = plRapid2.data.response.filter(mut=> coun.data.result.filter(c=> c.team_name === selCoun.data.response[0].team.name)[0].players.map(c=> c.player_name).includes(mut.player.name))
            let pls3 = plRapid3.data.response.filter(mut=> coun.data.result.filter(c=> c.team_name === selCoun.data.response[0].team.name)[0].players.map(c=> c.player_name).includes(mut.player.name))
            
            let allpls = [].concat(pls1,pls2,pls3)
            setPlayers(allpls)
        }
        getPlayers()
    }

    const onChangePL = plId => {
        setPlId(plId)
    }
    
    if(page){
        return (
            <div className='d-flex flex-column justify-content-center align-items-center' style={{height:"80vh"}}>
                <Form>
                <Form.Group controlId="formBasicEmail">
                    <Form.Label>Team Name</Form.Label>
                    <Form.Control 
                    style={teamName.trim().length<3 ? {border:"solid red"} : {border:"solid lime"}}
                    type="text" 
                    placeholder="Enter Your Team Name"
                    value={teamName}
                    onChange={e=>setTeamName(e.target.value)} />
    
                    <Form.Label className='pt-3'>Choose Your Tactic</Form.Label>
                    <select 
                    className='w-100 py-1'
                    onChange={e=>setTactic(e.target.value)}>
                    <option value="4-3-3">4-3-3</option>
                    <option value="3-5-2">3-5-2</option>
                    <option value="3-4-3">3-4-3</option>
                    <option value="5-3-2">5-3-2</option>
                    <option value="5-4-1">5-4-1</option>
                    <option value="4-4-2">4-4-2</option>
                    <option value="4-5-1">4-5-1</option>
                    </select>
    
                    <Button onClick={onPage} className='px-4 btn-success mt-4'>Next</Button>
               </Form.Group>
                </Form>
            </div>
        )
    } else{
        return(
            <div className='d-flex flex-column justify-content-center align-items-center'>

                <div className={seP === true ? "d-flex flex-column justify-content-center align-items-center modul active" : "d-flex flex-column justify-content-center align-items-center modul"}>
                   <div className='px-5 mx-5' style={{height:"60vh", background:"white"}}>
                       <div className='d-flex justify-content-between align-items-center'>
                           <p>Choose Player</p>
                           <img onClick={()=>setSeP(false)} className='text-right' style={{width:"40px", height:"40px", borderRadius:"50%"}} src={foto2} />
                           
                       </div>

                       
                        <select onChange={e=>OnChangeCoun(e.target.value)} className='w-100 py-5'>
                            {country.map(c=>(
                                <option value={c.team.id}>
                                    {c.team.name}
                                </option>
                            ))}
                        </select>

                        {players.length != 0 ? <select onChange={e=>onChangePL(e.target.value)} className='w-100 py-5'>
                            {players.map(c=>(
                                <option value={c}>
                                    {c.player.name}
                                </option>
                            ))}
                        </select> : ''}
                       

                        <button onClick={()=>console.log(coundId)}>Submit</button>
                   </div>
                </div>

            <div className='d-flex'>
                <h3 className='px-2'>{teamName}</h3>
                <h3 className='px-2 text-danger'>{tactic}</h3>
            </div>

            <div className='match-field d-flex flex-column justify-content-center align-items-center' style={{background: `url(${foto1}) no-repeat center /cover`, height:"90vh", width:"28rem"}}>
                <div className="d-flex justify-content-center align-items-center">
                {fr.map(f=>(
                    <div className='px-3 py-5 mt-5'>
                        <img onClick={()=>PlayerSelect(`f${f}`)} src={forv1 !== undefined ? forv1.player.photo : " "} style={{backgroundColor:"red",border:'none',borderRadius:'100%', width:"50px", height:"50px", cursor:"pointer"}} />
                        <small style={{display:"block"}}>{forv1 !== undefined ? forv1.player.name : "Forward"} 
                        <small className='text-warning'>{forv1 !== undefined ? ` ($${Math.floor((Math.floor(forv1.statistics[0].games.rating*10)/90)*100)})` : ""}</small>
                        </small>
                    </div>
                ))}
                </div>
                
                <div className="d-flex">
                {mid.map(m=>(
                    <div className='px-1 py-5 text-center'>
                        <img onClick={()=>PlayerSelect(`m${m}`)} src={mid1 !== undefined ? def1.player.photo : " "} style={{backgroundColor:"lime",border:'none',borderRadius:'100%', width:"50px", height:"50px", cursor:"pointer"}} />
                        <small style={{display:"block"}}>{mid1 !== undefined ? mid1.player.name : "Midfielder"}
                        <small className='text-warning'>{mid1 !== undefined ? ` ($${Math.floor((Math.floor(def1.statistics[0].games.rating*10)/90)*100)})` : ""}</small>
                        </small>
                </div>
                ))}
                </div>

                <div className="d-flex">
                {def.map(d=>(
                    <div className='px-2 pt-5 text-center'>
                    <img onClick={()=>PlayerSelect(`d${d}`)} src={def1 !== undefined ? def1.player.photo : " "} style={{backgroundColor:"rgb(8, 152, 235)",border:'none',borderRadius:'100%', width:"50px", height:"50px", cursor:"pointer"}} />
                    <small style={{display:"block"}}>{def1 !== undefined ? def1.player.name : "Defender"}
                    <small className='text-warning'>{def1 !== undefined ? ` ($${Math.floor((Math.floor(def1.statistics[0].games.rating*10)/90)*100)})` : ""}</small>
                    </small>
                </div>
                ))}
                </div>

                <div className='text-center'>
                    <div className='pt-5'>
                        <img onClick={()=>PlayerSelect(`g`)} src={def1 !== undefined ? def1.player.photo : " "} style={{backgroundColor:"orange",border:'none',borderRadius:'100%', width:"50px", height:"50px", cursor:"pointer"}} />
                        <small style={{display:"block"}}>{def1 !== undefined ? def1.player.name : "Goalkeeper"}
                                <small className='text-warning'>{def1 !== undefined ? ` ($${Math.floor((Math.floor(def1.statistics[0].games.rating*10)/90)*100)})` : ""}</small>
                        </small>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default compose(
    firestoreConnect([{collection : 'users'}]),
    connect((state, props)=>({
        users : state.firestore.ordered.users,
        team : state.firebase.profile.team
    }))
)(NewTeam)
