import React, {useState} from 'react'
import { Card, Col, Form, Row, Button } from 'react-bootstrap'
import { firebaseConnect } from 'react-redux-firebase'

const Register = props => {

    const [email, setEmail] = useState('')
    const [password, setPasword] = useState('')

    const{firebase, history} = props

    const onReg = e => {
        e.preventDefault()

        firebase.createUser({email, password}, {email, team: false}).catch(err=>alert(err)).then(()=>history.push('/'))
    }

    return (
        <div className='py-5'>
            <Row>
                <Col md={6} className='mx-auto'>
                    <Card className="bg-dark text-light py-4 px-2">
                        <Card.Body>
                            <h1 className="text-center text-light pb-4 pt-3">
                                <i className="fa fa-lock"></i> Register
                            </h1>
                            <Form onSubmit={onReg}>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label>Email address</Form.Label>
                                    <Form.Control type="email" placeholder="Enter email"
                                    value={email}
                                    onChange={e=>setEmail(e.target.value)} />
                                </Form.Group>

                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control type="text" placeholder="Enter email"
                                    value={password}
                                    onChange={e=>setPasword(e.target.value)} />
                                </Form.Group>

                                <Button variant="primary" type="submit">
                                    Register
                                </Button>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </div>
    )
}

export default firebaseConnect()(Register)
